const CTA = () => {
  return (
    <div className={s.ctaWrap}>
      <div className={s.wrapContent}>
        <div className={s.content}>
          <div className={s.contentTitle}>
            <h1>  ?</h1>
          </div>
          <div className={s.contentText}>
            <h3>
                ,      !    
                  .
            </h3>
          </div>
          <Button text="  " customClass={s.btn} />
        </div>
      </div>
    </div>
  );
};

export default CTA;
